import React from 'react';

export default function QuestionMarkIcon(props) {
    return(
        <svg className={props.className} onClick={props.onClick} role="img" aria-label="Question mark icon" width="34px" height="34px" viewBox="0 0 34 34" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
            <desc>Created with Sketch.</desc>
            <defs></defs>
            <g id="Page-1" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                <g id="Group-6" transform="translate(-1.000000, -1.000000)">
                    <circle id="Oval" fill="#FFFFFF" cx="18" cy="18" r="13"></circle>
                    <image id="q-mark" x="0" y="0" width="36" height="36" xlinkHref="data:image/png;base64,"></image>
                </g>
            </g>
        </svg>
    );
}





